<template>
  <div class="watermark-wrapper" style="position:relative">
    <slot />
    <div class="watermark" :class="markClassName" :style="watermarkStyle"></div>
  </div>
</template>

<script>
export default {
  props: {
    // 
    width: {
      type: Number,
      default: 120,
    },
    // 
    height: {
      type: Number,
      default: 64,
    },
    //  
    rotate: {
      type: Number,
      default: -22,
    },
    //  2  3 
    image: {
      type: String,
      default: "",
    },
    //  z-index
    zIndex: {
      type: Number,
      default: 9,
    },
    // 
    content: {
      type: String,
      default: "",
    },
    // 
    fontColor: {
      type: String,
      default: "rgba(0,0,0,.15)",
    },
    // 
    fontSize: {
      type: [String, Number],
      default: 16,
    },

    //////////////////////////

    // 
    markStyle: {
      type: Object,
      default: () => {},
    },
    // 
    markClassName: {
      type: String,
      default: "",
    },
    // 
    gapX: {
      type: Number,
      default: 212,
    },
    // 
    gapY: {
      type: Number,
      default: 222,
    },
    //  canvas , ,  offsetTop = gapX / 2
    offsetLeft: {
      type: Number,
      default: 0,
    },
    //  canvas ,  offsetTop = gapY / 2
    offsetTop: {
      type: Number,
      default: 0,
    },
    /**  */
    fontStyle: {
      type: String,
      default: "normal",
      validator: function(value) {
        // 
        return ["none", "normal", "italic", "oblique"].indexOf(value) !== -1;
      },
    },
    // 
    fontWeight: {
      type: [String, Number],
      default: "normal",
      validator: function(value) {
        // 
        return (
          ["normal", "light", "weight"].indexOf(value) !== -1 ||
          typeof value === "number"
        );
      },
    },
    // 
    fontFamily: {
      type: String,
      default: "sans-serif",
    },
  },
  data() {
    return {
      img: null,
    };
  },
  computed: {
    base64Url() {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      const ratio = this.getPixelRatio(ctx);

      const canvasWidth = `${(this.gapX + this.width) * ratio}px`;
      const canvasHeight = `${(this.gapY + this.height) * ratio}px`;
      const canvasOffsetLeft = this.offsetLeft || this.gapX / 2;
      const canvasOffsetTop = this.offsetTop || this.gapY / 2;

      canvas.setAttribute("width", canvasWidth);
      canvas.setAttribute("height", canvasHeight);

      if (ctx) {
        //  rotate
        ctx.translate(canvasOffsetLeft * ratio, canvasOffsetTop * ratio);
        ctx.rotate((Math.PI / 180) * Number(this.rotate));
        const markWidth = this.width * ratio;
        const markHeight = this.height * ratio;

        if (this.img) {
          ctx.drawImage(this.img, 0, 0, markWidth, markHeight);
          return canvas.toDataURL();
        } else if (this.content) {
          const markSize = Number(this.fontSize) * ratio;
          ctx.font = `${this.fontStyle} normal ${this.fontWeight} ${markSize}px/${markHeight}px ${this.fontFamily}`;
          ctx.fillStyle = this.fontColor;
          ctx.fillText(this.content, 0, 0);
          return canvas.toDataURL();
        }
      } else {
        // eslint-disable-next-line no-console
        console.error("Canvas");
      }

      return "";
    },
    watermarkStyle() {
      return {
        zIndex: this.zIndex,
        position: "absolute",
        left: "0px",
        top: "0px",
        width: "100%",
        height: "100%",
        backgroundSize: `${this.gapX + this.width}px`,
        pointerEvents: "none",
        backgroundRepeat: "repeat",
        backgroundImage: `url('${this.base64Url}')`,
        ...this.markStyle,
      };
    },
  },
  watch: {
    image: {
      handler() {
        if (this.image) {
          const img = new Image();
          img.crossOrigin = "anonymous";
          img.referrerPolicy = "no-referrer";
          img.src = this.image;
          img.onload = () => {
            this.img = img;
          };
        } else {
          this.img = null;
        }
      },
      immediate: true,
    },
  },
  methods: {
    getPixelRatio(context) {
      if (!context) {
        return 1;
      }
      const backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
  },
};
</script>
